import React from "react"
import {useState, useEffect} from "react";
import {useDispatch, useSelector} from "react-redux"
import { getAllPokemons, getAllTypes } from "../redux/actions";
import { Link, NavLink } from "react-router-dom";
import searchBar from "./SearchBar";
import PokemonCard from "./PokemonCard";
import s from "../styles/home.module.css"



export default function Home () {
    const dispatch = useDispatch();
    const types = useSelector((state)=> state.types);
    
    useEffect(()=>{
        dispatch(getAllTypes());
    },[]);

    useEffect(()=>{
        dispatch(getAllPokemons());
    },[]);
    const allPokemons = useSelector((state)=>state.pokemons);
   console.log(allPokemons)

    return(
        <div className={s.home}>
            <Link to="/pokemons"></Link>
            <h1>Pokemon App</h1>
            <div>
                <br></br>
                <div>
                    <span>Name</span>
                    <select>
                        <option value ="asc">A-Z</option>
                        <option value ="desc">Z-A</option>
                    </select>
                    <span>Stats</span>
                    <select>
                        <option value ="asc">Highest HP</option>
                        <option value ="desc">Lower HP</option>
                        <option value ="asc">Highest Attack</option>
                        <option value ="desc">Lower Attack</option>
                        <option value ="asc">Highest Defense</option>
                        <option value ="desc">Lower Defense</option>
                        <option value ="asc">Highest Special Attack</option>
                        <option value ="desc">Lower Special Attack</option>
                        <option value ="asc">Highest Special Defense</option>
                        <option value ="desc">Lower Special Defense</option>
                        <option value ="asc">Highest Speed</option>
                        <option value ="desc">Lower Speed</option>
                    </select>
                    <span>Types</span>
                    <select>
                       <option value = "All">All</option>
                       {types?.map(e =>{
                        return (
                            <option value = {e.name} key = {e.id}>
                                {e.name}
                            </option>
                        )
                       })}
                       
                    </select>
                </div>
                <br></br>
                    <select>
                        <option value ="all">All Pokemon</option>
                        <option value ="created">Pokemon Created</option>
                        <option value ="api">Existing Pokemon</option>
                    </select>
            </div>

               <div className={s.cardContainer}>
                {allPokemons?.map((e)=> {
                return(
                    <PokemonCard 
                        key={e.id}
                        id={e.id}
                        name={e.name}
                        sprite={e.sprite}
                        type={e.type}
                    />
                )
               })} 
            </div>
        </div>
    )

}