import React from "react";
import { Link } from "react-router-dom";
import s from '../styles/pokemonCard.module.css'
import "../styles/prueba.css"

export default function PokemonCard ({name, sprite, type, id}) {
   
    return(

        <div className={s.cardContainer}>
            <Link className={s.subrayado} to={`/home/${id}`}>
                <h3 className={s.subrayado}>{name}</h3>
                <img src= {sprite} alt= "img" width="180px" height="130px"/>
            </Link>
            <ul className={s.ul}>
                {
                 type?.map(e=>{
                    return(
                        <li className={`${e} ${s.li}`}>{e}</li>

                    )
                 })
                }
            </ul>
        </div>
    )
}